.button{min-width:101px;color:#fff;text-align:center;text-transform:uppercase;font-size:14px;transition:.3s transform;border:none;background-color:transparent;cursor:pointer}.button__long{display:block;width:170px;margin:45px auto 0 auto}.button .inner{position:relative;background-color:#9f0013;line-height:18px;padding:0 18px;transition:none}.button .inner::before{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;left:0;top:-10px;transition:none}.button .inner::after{border-color:#9f0013 transparent;border-style:solid;border-width:0 0 10px 10px;content:"";display:block;position:absolute;right:0;bottom:-10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);transition:none}.button__main:hover,.button__secondary:hover{color:#fff}.button__secondary .inner{background-color:#5c5c5c}.button__secondary .inner::before{border-color:#5c5c5c transparent;transition:none}.button__secondary .inner::after{border-color:#5c5c5c transparent;transition:none}.button::before{content:"";display:block;height:10px;margin-left:10px;transition:none}.button::after{content:"";display:block;height:10px;margin-right:10px;transition:none}.button.button__main::before,.button.button__main::after{background-color:#9f0013}.button.button__secondary::before,.button.button__secondary::after{background-color:#5c5c5c}.button:hover{-webkit-transform:translateY(-5px);transform:translateY(-5px)}*{font-family:"Roboto Condensed",sans-serif;margin:0;padding:0;box-sizing:border-box}ul,li,dl{list-style-type:none;margin:0;padding:0}a{text-decoration:none;color:inherit;display:inline-block}a:hover{text-decoration:none;color:inherit}p{margin:0;padding:0}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.app{margin:0 auto;padding:0px 0 50px 0;position:relative}.app .bg-decoration{position:absolute;right:-174px;bottom:-70px;z-index:-1}main{margin-top:50px;position:relative}.pulse{-webkit-animation:pulse 1.5s ease-in-out .5s infinite;animation:pulse 1.5s ease-in-out .5s infinite}@-webkit-keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}@keyframes pulse{0%{opacity:1}50%{opacity:.4}100%{opacity:1}}
